@import 'vanilla-framework/scss/vanilla';
@include vanilla;

.p-card {
  margin-top: 50px;
  padding: 0px;
  display: flex;
  flex-direction: column;
}

.card-header {
  padding-top: 20px;
  border-top: 3px solid #666;
}

.p-card_content {
  margin: 1rem;
}

.p-card_header {
  border-top: 3px solid #666;
  padding-top: 1rem;
  padding-left: 1rem;
}

.p-card_footer {
  margin: 1rem;
  margin-top: auto;
}



